@import url(https://fonts.googleapis.com/css2?family=Prompt:ital,wght@0,300;0,500;0,700;1,300;1,500;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --font-size-smaller: 14px;
  --font-size-standard: 16px;
  --font-size-bigger: 18px;
  --border-radius: 5px;
  --border-color: rgb(204, 204, 204);
  --light-shadow: 0 1px 5px rgba(0, 0, 0, 0.05);
  --heavy-shadow: 0 1px 15px rgba(0, 0, 0, 0.1);
  --light-grey: rgba(238, 238, 238, 0.5);
  --light-grey-no-opacity: rgb(238, 238, 238);
}

* {
  font-family: 'Prompt', sans-serif;
  font-size: 16px;
  font-size: var(--font-size-standard);
  box-sizing: border-box;
}

.App {
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 10px 5% 2%;
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

.display-none {
  display: none;
}

.loader-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.showSuggestions {
  display: block;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 5px;
  border-radius: var(--border-radius);
  outline: none;
  border: 1px solid #ccc;
  line-height: 20px;
  align-self: center;
  height: 38px;
}

.search-bar {
  position: sticky;
  top: 0;
  display: flex;
  flex-direction: row;
  padding: 20px 0 30px;
  background: white;
  border-bottom: 1px solid rgb(204, 204, 204);
  border-bottom: 1px solid var(--border-color);
  z-index: 1;
}

.city-search {
  display: flex;
  width: 100%;
  position: relative;
  margin-right: 10px;
}

.city-search__input {
  width: 100%;
}

.city-search__suggestions {
  visibility: hidden;
  opacity: 0;
  width: 100%;
  max-height: 60vh;
  position: absolute;
  z-index: 2;
  align-self: center;
  margin: 5px 0 0;
  top: 43px;
  box-shadow: 0 1px 15px rgba(0, 0, 0, 0.1);
  box-shadow: var(--heavy-shadow);
  overflow: scroll;
  background-color: rgb(238, 238, 238);
  background-color: var(--light-grey-no-opacity);
  border-radius: 5px;
  border-radius: var(--border-radius);
  border: 1px solid rgb(204, 204, 204);
  border: 1px solid var(--border-color);
  transition: visibility 0.15s, opacity 0.15s linear;
}

.city-search__suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  cursor: default;
  transition-duration: 0.2s;
}

.city-search__suggestions > li:last-child {
  border-bottom-left-radius: 5px;
  border-bottom-left-radius: var(--border-radius);
  border-bottom-right-radius: 5px;
  border-bottom-right-radius: var(--border-radius);
}

.city-search__suggestions > li:hover {
  background-color: #bcdcdb;
}

.city-search__button {
  background: none;
  border: none;
  width: 100%;
  text-align: left;
}

.city-search__button:hover {
  cursor: pointer;
}

.city-search__all-button {
  background: none;
  border: none;
  border-top: 1px solid rgb(204, 204, 204);
  border-top: 1px solid var(--border-color);
  width: 100%;
  text-align: center;
  margin-top: 0;
  padding-top: 8px;
}

.city-search__all-button:hover {
  cursor: pointer;
}

.number-of-events {
  display: flex;
  justify-content: flex-end;
  font-size: 14px;
  font-size: var(--font-size-smaller);
  width: 50px;
  margin: auto;
}

.number-of-events__input {
  text-align: center;
  border: none;
  background: none;
  cursor: pointer;
}

.event-list {
  margin: 0;
}

.event {
  padding: 10px;
  min-width: 250px;
  margin: 3vh 0;
  border: 1px solid rgb(204, 204, 204);
  border: 1px solid var(--border-color);
  border-radius: 5px;
  border-radius: var(--border-radius);
  position: relative;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.05);
  box-shadow: var(--light-shadow);
  background-color: rgba(221, 246, 255, 0.1);
  transition-duration: 0.15s;
}

.event:hover,
.event:focus {
  -webkit-transform: scale(1.003);
          transform: scale(1.003);
}

.event__window {
  text-align: left;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  background: none;
  border: none;
  outline: none;
  cursor: unset;
}

.event__show-more-button {
  width: 100%;
  padding: 10px 0 0;
  text-align: center;
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
  transition-duration: 0.01s;
}

.turn {
  -webkit-transform: rotate(-0.5turn);
          transform: rotate(-0.5turn);
}

.event__summary {
  font-size: 18px;
  font-size: var(--font-size-bigger);
  font-weight: 600;
}

.event__details {
  text-align: justify;
  font-weight: 300;
  background-color: rgba(238, 238, 238, 0.5);
  background-color: var(--light-grey);
  border-radius: 5px;
  border-radius: var(--border-radius);
  padding: 5px 10px;
}

.group-name {
  text-transform: uppercase;
}

.going,
.visibility {
  font-style: italic;
}

.event__description {
  overflow-x: hidden;
}

.button-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
  font-family: sans-serif;
}

.Alert {
  position: fixed;
  top: 20px;
}

.data-vis-wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.data-vis-wrapper .recharts-responsive-container {
  min-width: 100%;
  display: flex;
}

.recharts-surface {
  overflow: visible;
}

.login-button {
  color: #494949;
  text-transform: uppercase;
  text-decoration: none;
  background: #fff;
  padding: 20px;
  border: 5px solid #494949;
  border: var(--border-radius) solid #494949;
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
}

.google-btn {
  width: 184px;
  height: 42px;
  margin: 1rem;
  background-color: #4285f4;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}

.google-btn:hover {
  box-shadow: 0 0 6px #4285f4;
}

.google-btn:active {
  background: #1669f2;
}

.google-icon-wrapper {
  position: absolute;
  margin-top: 1px;
  margin-left: 1px;
  width: 40px;
  height: 40px;
  border-radius: 2px;
  background-color: #fff;
}

.google-icon {
  position: absolute;
  margin-top: 11px;
  margin-left: -8px;
  width: 18px;
  height: 18px;
}

.btn-text {
  float: right;
  margin: 11px 11px 0 0;
  color: #fff;
  letter-spacing: 0.2px;
  font-family: "Roboto";
}

@media only screen and (min-width: 992px) {
  .data-vis-wrapper {
    flex-direction: row;
  }

  .data-vis-wrapper .recharts-responsive-container {
    flex-basis: 50%;
    max-width: 50%;
    min-width: auto;
    margin: 0 auto;
  }
}

/* Make clicks pass-through */
#nprogress {
  pointer-events: none;
}

#nprogress .bar {
  background: #29d;

  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;

  width: 100%;
  height: 2px;
}

/* Fancy blur effect */
#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px #29d, 0 0 5px #29d;
  opacity: 1.0;

  -webkit-transform: rotate(3deg) translate(0px, -4px);
          transform: rotate(3deg) translate(0px, -4px);
}

/* Remove these to get rid of the spinner */
#nprogress .spinner {
  display: none;
  position: fixed;
  z-index: 1031;
  top: 15px;
  right: 15px;
}

#nprogress .spinner-icon {
  width: 18px;
  height: 18px;
  box-sizing: border-box;

  border: solid 2px transparent;
  border-top-color: #29d;
  border-left-color: #29d;
  border-radius: 50%;

  -webkit-animation: nprogress-spinner 400ms linear infinite;
          animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}

@-webkit-keyframes nprogress-spinner {
  0%   { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}
@keyframes nprogress-spinner {
  0%   { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


